@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #d2bf9b;
  --color-primary: #6b583e;
  --color-primary-variant: #c5d39b;
  --color-white: #ffffff;
  --color-dark: #527547;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 400ms ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
}

/*GENERAL STYLES*/

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 4rem;
  height: auto;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-dark);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-dark {
  color: var(--color-dark);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4 rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/*CAROUSEL*/

.carousel {
  padding: 1rem;
}

.testing {
  background-color: rgb(255, 196, 230);
}

.slick-track {
  display: flex !important;
  /* gap:0.5rem;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: stretch;  */
  /* background-color: #527547; */
}

.slick-slide {
  background-color: var(--color-dark);
  border-radius: 0.5rem;
  margin: 0px 5px;
  height: inherit !important;
  /* display: flex; */
}

.slick-slide > div {
  height: 100%;
  display: flex;
  /* background: var(--color-bg); */
}
.slick-slide > div > div {
  /* background: var(--color-primary-variant); */
  height: 100%;
}

.slick-dots {
  padding: 5rem;
  /* color: var(--color-white) */
}

.slick-list {
  /* color: var(--color-white); */
  /* margin: 0 -10px; */
  /* background-color: beige; */
  /* padding: 2rem; */
}

.content {
  /* padding: 20px; */
  /* margin: auto; */
  /* background-color: blue; */
}

.carousel_card {
  background-color: var(--color-dark);
  border-radius: 0.5rem;
  color: var(--color-white);
  height: inherit !important;
  transition: var(--transition);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr min-content;
}

.carousel_card:hover .carousel_card_text {
  cursor: default;
  opacity: 1;
}

/* MEDIA QUERIES (MEDIUM DEVICES)*/

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
}

/* MEDIA QUERIES (SMALL DEVICES)*/

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-top: 3rem;
  }

  .slick-slide {
    margin: 0;
  }
}
